import * as React from "react";
import { Component } from "react";

import { Dispatch } from "redux";
import { connect } from "react-redux";
import keydown from "react-keydown";

import CorrectModal from "components/CorrectModal";
import Input from "components/Input";

import { updatePlayerHP, updateEnemyHP } from "reduxStore/actions";

import { QuestionInterface } from "utils/types";

interface Props {
  question: QuestionInterface;
  dispatch: Dispatch;
}

interface State {
  isOpen: boolean;
  isCorrect: boolean;
}

class Question extends Component<Props, State> {
  constructor(props) {
    super(props);

    this.checkAnswer = this.checkAnswer.bind(this);
    this.dismissModal = this.dismissModal.bind(this);
  }

  answerInput: Input;

  state = {
    isOpen: true,
    isCorrect: null
  };

  @keydown("enter")
  checkAnswer() {
    const { correctAnswer } = this.props.question;
    const userAnswer = this.answerInput.input.value;

    this.setState(prevState => ({
      ...prevState,
      isCorrect: userAnswer === correctAnswer
    }));

    if (userAnswer === correctAnswer) {
      this.props.dispatch(updateEnemyHP(-10));
    } else {
      this.props.dispatch(updatePlayerHP(-10));
    }

    this.dismissModal();
  }

  dismissModal() {
    this.setState(prevState => ({
      ...prevState,
      isOpen: false
    }));
  }

  render() {
    return (
      <>
      <CorrectModal />
        <div
          className={`questions-outer ${!this.state.isOpen ? "hide" : ""}`}
          onKeyDown={this.checkAnswer}
        >
          <div className="questions-inner">
            <h1>שאלה מספר {this.props.question.id}</h1>
            <p>{this.props.question.question}</p>
            <span>{this.props.question.sentence}</span>
            <div className="answer">
              <Input
                type="text"
                label={this.props.question.answerLabel}
                autofocus
                ref={input => (this.answerInput = input)}
              />
            </div>
            <p className="call-to-action">
              לחץ על מקש האנטר בכדי לבדוק את התשובה
            </p>
          </div>
          <style jsx>{`
            .questions-outer {
              position: absolute;
              min-height: 100%;
              min-width: 100%;
              background: rgba(0, 0, 0, 0.4);
              z-index: 2;
            }

            .questions-outer.hide {
              display: none;
            }

            .questions-inner {
              direction: rtl;
              text-align: center;
              background: #fff;
              width: 90%;
              height: 70%;
              padding: 2rem 4rem;
              margin: 10rem auto;
              border-radius: 2px;
              box-shadow: 0 0 6px 3px rgba(0, 0, 0, 0.15);
            }

            .questions-inner h1 {
              font-size: 2.5rem;
            }

            .questions-inner p {
              font-size: 3rem;
              margin: 0;
            }

            .questions-inner span {
              font-size: 4rem;
              font-weight: bold;
            }

            .questions-inner .answer {
              font-size: 2rem;
              width: 50%;
              margin: 1.5rem auto;
            }

            .questions-inner p.call-to-action {
              font-size: 1.5rem;
              color: #9c9c9c;
              margin: 1rem 0 0;
            }
          `}</style>
        </div>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { player, enemy } = state;
  return { player, enemy };
}

export default connect(mapStateToProps)(Question);
