import * as React from "react";
import { Component } from "react";

import { QuestionInterface } from "utils/types";

interface Props {
  wasCorrect: boolean;
  question: QuestionInterface;
}

interface State {
  isOpen: boolean;
}

class Question extends Component<Props, State> {
  constructor(props) {
    super(props);

    this.dismissModal = this.dismissModal.bind(this);
  }

  state = {
    isOpen: true
  };

  dismissModal() {
    this.setState(prevState => ({
      ...prevState,
      isOpen: false
    }));
  }

  render() {
    return (
      <div
        className={`correct-modal-outer ${!this.state.isOpen ? "hide" : ""}`}
      >
        <div className="correct-modal-inner">
          <h1>{this.props.wasCorrect ? "צדקת!" : "טעית!"}</h1>
          <p>{this.props.question.question} במשפט</p>
          <span>
            ה{this.props.question.answerLabel} הוא{" "}
            {this.props.question.correctAnswer}
          </span>
          <p className="call-to-action">לחץ על כל מקש בכדי להמשיך</p>
        </div>
        <style jsx>{`
          .correct-modal-outer {
            position: absolute;
            min-height: 100%;
            min-width: 100%;
            background: rgba(0, 0, 0, 0.4);
            z-index: 2;
          }

          .correct-modal-outer.hide {
            display: none;
          }

          .correct-modal-inner {
            direction: rtl;
            text-align: center;
            background: #fff;
            width: 90%;
            height: 70%;
            padding: 2rem 4rem;
            margin: 10rem auto;
            border-radius: 2px;
            box-shadow: 0 0 6px 3px rgba(0, 0, 0, 0.15);
          }

          .correct-modal-inner h1 {
            font-size: 2.5rem;
          }

          .correct-modal-inner p {
            font-size: 3rem;
            margin: 0;
          }

          .correct-modal-inner span {
            font-size: 4rem;
            font-weight: bold;
          }
          
          .correct-modal-inner p.call-to-action {
            font-size: 1.5rem;
            color: #9c9c9c;
            margin: 1rem 0 0;
          }
        `}</style>
      </div>
    );
  }
}

export default Question;
